import { createClient } from '@supabase/supabase-js';
import { cookies } from 'next/headers';

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL;
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY;

export async function GET() {
  try {
    const cookieStore = await cookies();
    const authCookie = cookieStore.get('sb-ytzzsjhqmtijrjzngfhv-auth-token')?.value;

    if (!authCookie) {
      return Response.json({ data: null, error: 'Not authenticated' }, { status: 401 });
    }

    // Decode session and extract access token
    let session, accessToken;
    try {
      if (authCookie.startsWith('base64-')) {
        const base64Data = authCookie.substring(7);
        session = JSON.parse(Buffer.from(base64Data, 'base64').toString('utf-8'));
      } else {
        session = JSON.parse(authCookie);
      }
      accessToken = session?.access_token;
    } catch {
      return Response.json({ data: null, error: 'Invalid session format' }, { status: 401 });
    }

    if (!accessToken) {
      return Response.json({ data: null, error: 'No access token' }, { status: 401 });
    }

    // Create authenticated Supabase client
    const supabase = createClient(supabaseUrl, supabaseAnonKey, {
      global: { headers: { Authorization: `Bearer ${accessToken}` } },
    });

    // Get user info
    const { data: { user }, error: userError } = await supabase.auth.getUser();
    if (!user || userError) {
      return Response.json({ data: null, error: 'User not found' }, { status: 401 });
    }

    // Query shoots for this user (RLS should enforce security)
    const { data, error } = await supabase
      .from('shoots')
      .select('*')
      .eq('user', user.id)
      .order('date', { ascending: false })
      .order('time', { ascending: false });

    if (error) {
      return Response.json({ data: null, error: error.message }, { status: 500 });
    }

    return Response.json({ data, error: null }, { status: 200 });
  } catch (error) {
    return Response.json({ data: null, error: error.message }, { status: 500 });
  }
}